import vTab from "@/components/v-tab/v-tab.vue";
export default {
	components:{vTab},
	data() {
		return {
			title: 'Hello',
			position(value){
				if(value == -1){
					return "取消订单";
				}
				if(value == 1){
					return "待支付";
				}
				if(value == 2){
					return "待发货";
				}
				if(value == 3){
					return "待收货";
				}
				if(value == 0){
					return "交易成功";
				}
			},
			state:'all',
			dataNav:[
				{
					name:'全部',
					value:'all'
				},
				{
					name:'待支付',
					value:1
				},
				{
					name:'待发货',
					value:2
				},
				{
					name:'待收货',
					value:3
				},
				{
					name:'交易成功',
					value:0
				},
				{
					name:'取消订单',
					value:-1
				}
				
			],
			data:[]
			
		}
	},
	onLoad() {
		this.bill();
	},
	methods: {
		getP(s){
			this.$ajax("/order/finish","POST",{orderNo:s.orderNo}).then(res=>{
				if(res.code == "200"){
					uni.showToast({
						"title":"确认收货成功",
						"icon":"none"
					})
					setTimeout(function(){
						uni.reLaunch({
							"url":"/pages/mine/bill"
						})
					},888)
					
				}
			})
		},
		pay(s){
			this.$ajax("/order/buy","POST",{addressId:_this.address.id}).then(res=>{
				if(res.code == 200){
					uni.requestPayment({
						provider: "wxpay",
						timeStamp:res.paymentResult.payment.timeStamp,
						nonceStr:res.paymentResult.payment.nonceStr,
						package:res.paymentResult.payment.package,
						signType:res.paymentResult.payment.signType,
						paySign:res.paymentResult.payment.paySign,
						success:function(){
							// uni.setStorageSync('qrcodeNo',res.no);
							uni.navigateTo({
								"url":"/pages/mine/billdetail/?orderNo="+res.paymentResult.no
							})
							
						},
						fail:function(){
							uni.showToast({
								"title":"取消支付,将放入待支付队列",
								"icon":"none"
							})
							setTimeout(function(){
								uni.navigateTo({
									"url":"/pages/mine/billdetail/?orderNo="+res.paymentResult.no
								})
							},999)
						},
					})
				}else{
					uni.showToast({
						"title":res.msg,
						"icon":"none"
					})
				}
				})
		},
		cancelBill(s){
			
		},
		toTab(url){
			uni.reLaunch({
				"url":"/pages/shop/shop"
			})
		},
		toUrl(url){
			uni.navigateTo({
				"url":url
			})
		},
		bill(state){
			let _this = this;
			_this.data = [];
			if(_this.state == 'all'){
				this.$ajax("/order/page","POST",{pageNo:1,pageSize:10}).then(res=>{
					res.list.map(res=>{
						_this.data.push(res);
					})
					console.log(_this.data);
				})
			}else{
				this.$ajax("/order/page","POST",{pageNo:1,pageSize:10,state:_this.state}).then(res=>{
					res.list.map(res=>{
						_this.data.push(res);
					})
					console.log(_this.data);
				})
			}
			
		},
		changeIndex(item){
			// this.status = item.value;
			console.log(item);
			this.state = item.value;
			this.bill()
			// if(){
				
			// }
		},
		detail(s){
			uni.showLoading({
				"title":"加载中"
			})
			uni.hideLoading();
			uni.navigateTo({
				"url":"/pages/mine/billdetail?orderNo="+s.orderNo
			})
			
			
		}
	},
	filters:{
		position(value){
			if(value == -1){
				return "取消订单";
			}
			if(value == 1){
				return "待支付";
			}
			if(value == 2){
				return "待发货";
			}
			if(value == 3){
				return "待收货";
			}
			if(value == 0){
				return "交易成功";
			}
			if(value == -1){
				return "取消订单";
			}
		},
		size(value){
			let sizeArr = JSON.parse(value);
			let size = "";
			sizeArr.map((res)=>{
				size = size + " " + res.value 
			})
			return size;
		}
	}
}
